@use 'variables' as v;
@use 'mixins';

.modal-gallery{
    
    width: 550px;
    // &__background{
    //     @include mixins.modal-background;
    //     place-content: center;
    // }
    &__close-container{
        
        text-align: right;
        height: 30px;
    }
    &__close{
        width: 16px;
        &:hover{
            cursor: pointer;
            // content: url('../img/icon-close.svg');
        }
        
    }
    &__image-container{
        // background-image: url('../img/icon-menu.svg');
        height: 446px;
        background-size: cover;
        background-position: center;
        border-radius: 16px;
        margin-bottom: 32px;
        position: relative;
    }

    &__thumnails{    
        display: flex;
        justify-content: space-between;
        width: 450px;
        margin: auto;
    }
    &__thumnail{
        width: 90px;
        border-radius: 16px;
        &:hover{
            cursor: pointer;
            border: 2px solid v.$Orange;
        }
    }
    &__previus{
        @include mixins.image-btn(190px, -15px, true)
    }
    &__next{
        @include mixins.image-btn(190px, 520px, true)
    }
}